

import Vue from 'vue'
import App from './App'
import {router} from './router'
import axios from 'axios';
import store from './store/index';
Vue.config.productionTip = false;


import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(AlertPlugin);
Vue.use(LoadingPlugin);
Vue.use(ToastPlugin);

Vue.prototype.$http = axios;
axios.interceptors.request.use(function (config) {
vuxLoading.show();
return config;
}, function (error) {
vuxLoading.hide();
return Promise.reject(error);
});
axios.interceptors.response.use(function (response) {
setTimeout(x => vuxLoading.hide(), 0);

return response;
}, function (error) {
vuxLoading.hide();
return Promise.reject(error);
});


/* eslint-disable no-new */
let instance = new Vue({
el: '#app',
router,
template: '<App/>',
components: {App},
store
});
window.vuxLoading = instance.$vux.loading;



